import React, { Component } from "react";

export class Tamrow extends Component {
  constructor() {
    super();
    this.state = {
      icon: "far fa-heart hrt-ico",
      count: 0,
    };
  }
  like = (id) => {
    if (document.getElementById(id).classList[2] === "hrt-ico") {
      document.getElementById(id).classList.remove("hrt-ico");
      document.getElementById(id).classList.add("hrt-ico-hover");
    } else {
      document.getElementById(id).classList.remove("hrt-ico-hover");
      document.getElementById(id).classList.add("hrt-ico");
    }
  };
  render() {
    return (
      <div className="tam_component_col">
        {this.props.img.map((image) => {
          return (
            <div className="card-container">
              <img
                src={image}
                alt="tam_image"
                data-aos="fade-up"
                data-aos-duration={this.props.du}
              />
            </div>
          );
        })}
      </div>
    );
  }
}

export default Tamrow;
